<script>
    import MainLayout from "@/components/Common/MainLayout";
    import slideTypeSub from "@/components/R2Investment/SlideTypeSub";
    import slideTypeSubMain from "@/components/Common/SlideTypeSubMain";
    import popupType01 from "@/components/Popup/PopupType01";
    import listFundItem from "@/components/Investment/ListFundItem";
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    export default {
        name: "F-M-IN-002",
        components: {
            MainLayout,
	        slideTypeSub,
	        slideTypeSubMain,
	        listFundItem,
	        popupType01,
	        swiperSlide,
        },
        data() {
        	return{
                showAlertAlarm : false,
            }
        },
        methods : {
        	setAlarm(event) {
        		const $el = event.target;
                const flag = $el.classList.contains('active');
                const $this= this;
        		if(flag) {
                    $el.classList.remove('active');
                }else {
                    $el.classList.add('active');
                    $this.showAlertAlarm = !$this.showAlertAlarm;
                }
            }
        }
    }
</script>

