<template>
    <div class="wrap_swiper_box">
        <swiper :options="swiperOption" class="swiper_box">
            <swiper-slide class="swiper_item" v-for="item in slideArray" :key="item.id" :style="{backgroundImage : setImageUrl(item.imageUrl)}">
                <router-link class="wrap_item" v-bind:to="item.link" v-bind:class="item.id" >
                    <h2 class="title_info" v-html="alignLine(item.title)"></h2>
                </router-link>
            </swiper-slide>
            <div class="swiper-pagination black" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import { swiper, swiperSlide } from 'vue-awesome-swiper';
    export default {
        name: "SlideTypeSub",
        components: {
            swiper,
            swiperSlide,
        },
        props : [ 'slideArray'],
	    computed : {

        },
        data() {
            return {
                swiperOption: {
	                slidesPerView: 1.1,
	                spaceBetween: 10,
	                centeredSlides :false,
                    pagination: {
                        el: '.swiper-pagination.black'
                    }
                },
            }
        },
        methods: {
	        alignLine(text) {
		        return text.replace('\n', '<br/>')
	        },
	        setImageUrl : function(imageUrl) {
	        	const url = imageUrl;
		        return 'url(' + url + ')'
	        }
        },
    }
</script>

<style scoped>
</style>
