<template>
    <div class="wrap_swiper_box">
        <swiper :options="swiperOption" class="swiper_box">
            <slot name="content"></slot>
            <div class="swiper-pagination white" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import { swiper } from 'vue-awesome-swiper';
    export default {
        name: "SlideTypeSubMain",
        components: {
            swiper,
        },
	    data() {
		    return {
			    swiperOption: {
				    slidesPerView: 1,
				    spaceBetween: 10,
				    pagination: {
					    el: '.swiper-pagination.white'
				    },
                    centeredSlides : true,
			    },
		    }
	    },
    }
</script>

