<template>
    <div class="wrap_progress_bar" :class="{goal_fulfill : isMaximum}">
        <div class="wrap_graph">
            <div class="graph" :style="{'--width': calculatorGraph(percent) + '%'}"></div>
        </div>
        <div class="wrap_graph_text" ref="text">
            <div class="text_funding">현재 {{ count }}명 투자</div>
            <div class="text_goal" :style="{left: calculatorPosition(percent, 'elText01') + '%'}" ref="elText01">{{percent}}% {{isMaximum ? '달성': '달성중'}}</div>
        </div>
    </div>
</template>

<script>
	export default {
		name: "ProgressBar",
        props : [ 'percent', 'count' ],
        data() {
		    return{
		    	isMaximum : this.percent >= 100
            }
        },
        methods : {
	        calculatorGraph(percent) {
		        let position  = percent;
		        if(percent > 100) {
			        position = 100;
		        }
		        return position;
	        },
	        calculatorPosition : function(percent, el) {
		        const goalPercent = percent;
		        const scrollLeft = 320;
		        this.$nextTick(function () {
			        const target = this.$refs[`${el}`];
			        const wrapperWidth = target.parentElement.clientWidth;
			        const elWidth = target.clientWidth;
			        let posLeft = 'calc(' + goalPercent + '% - ' + (elWidth / 2) + 'px)';
			        let total = elWidth + (scrollLeft * percent / 100 - elWidth / 2);
			        let position = posLeft;
			        if(total >= wrapperWidth) {
				        position = 'calc(100% - ' + (elWidth) + 'px)'
			        }
			        target.style.left = position;
		        })
	        }
        }
	}
</script>

